










































import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { ContainerFocusInjectMixin } from './ContainerFocusService'

export default {
  name: 'VbCard',
  components: { FontAwesomeIcon },
  mixins: [
    ContainerFocusInjectMixin,
  ],
  data: () => ({
    show: true,
    cardStyleTemp: null,
  }),
  props: {
    noPadding: Boolean,
    dashed: Boolean,
    title: String,
    refresh: Boolean,
    dark: Boolean,
    width: String,
    height: String,
    color: String,
  },
  computed: {
    computedStyle () {
      if (this.cardStyleTemp) {
        return {
          ...this.cardStyleTemp,
          backgroundColor: this.color,
        }
      }

      return {
        height: this.height,
        width: this.width,
        backgroundColor: this.color,
      }
    },
    computedClass () {
      return {
        'VbCard--no-padding': this.noPadding,
        'VbCard--dashed': this.dashed,
        'VbCard--dark': this.dark,
      }
    },
  },
  methods: {
    doRefresh () {
      const { width, height } = window.getComputedStyle(this.$el)
      this.cardStyleTemp = { width, height }
      this.show = false

      setTimeout(() => {
        this.cardStyleTemp = null
        this.show = true
      })
    },
  },
}
